@import url('https://fonts.googleapis.com/css?family=Open+Sans:100,200,300,400,500,600,700,800,900&subset=cyrillic');
html {
    width: 100%;
	height: 100%;
}
body {
    width: 100%;
	height: 100%;
	overflow: hidden;
}
canvas {
    position: absolute;
}
#wrap {
    position: absolute;
	width: 100%;
	height: 100%;
	top: 0px;
	left: 0px;
	display: table;
}
#useTheMouseWheel {
    position: relative;
    display: table-cell;
	vertical-align: middle;
	color: white;
	text-align: center;
	font-family: Open Sans;
    font-size: 40px;
    letter-spacing: 4px;
	font-weight: 100;
	opacity: 1;
	transition: opacity 1s ease 2s;
	&.-hidden {
		opacity: 0;
	}
}
#preLoader {
    position: fixed;
	width: 100%;
	height: 100%;
	top: 0px;
	left: 0px;
	background-color: white;
	z-index: 100;
	opacity: 1;
	&.-hidden {
	    width: 0px;
		height: 0px;
		z-index: 0;
		opacity: 0;
	}
}
@keyframes spinner {
    from {transform: scale(1.5) rotate(0deg);}
    to {transform: scale(1.5) rotate(360deg);}
}
#spinner:before {
    content: '';
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    height: 30px;
    margin-top: -15px;
    margin-left: -15px;
    border-radius: 50%;
    border: 1px solid #ccc;
    border-top-color: #5449c1;
    animation: spinner .6s linear infinite;
}